#landing-section {
  @apply flex flex-col w-full items-center pt-6;
  background: url('../assets/img/starlight.png') no-repeat center top;

  .landing-logo {
    @apply mb-12 sm:mb-32;
  }
  ul {
    @apply flex flex-col items-center font-anton text-center text-white text-2xl sm:text-[1.5rem];
    li {
      @apply flex flex-col items-center; 
    }

    li:not(:last-child)::after {
      content: '';
      @apply divider my-2;
    }
  }

  &::after {
    background: unset !important;
  }
}

@media (min-height: 800px) and (max-height: 1300px) {
  #landing-section {
    @apply h-[90vh];
  }
}
